footer {
    background: transparent;
    padding-top: 40px;
    padding-bottom: 20px;
    position: relative;
    color: #636363;
    border-bottom: none;
    font-size: 12px;
    z-index: 2;
    line-height: 24px;
    border-top: 1px solid #cfcfcf;
}

.wpbingo-newsletter.newsletter-default .content-newsletter {
    position: relative;
}
.wpbingo-newsletter.newsletter-default .content-newsletter {
    max-width: 690px;
    margin: auto;
    display: flex;
}
.content-newsletter .wpcf7-form-control-wrap {
    width: 100%;
}

.wpbingo-newsletter.newsletter-default .content-newsletter input[type=email] {
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -ms-border-radius: 0;
    -o-border-radius: 0;
    background: #f3f3f3;
    height: 42px;
    line-height: 42px;

    width: 100%;
    border: 0;
    padding: 0 20px;
    font-size: 12px;
}

.wpbingo-newsletter.newsletter-default .content-newsletter .clearfix {
    position: relative;
    top: 0;
    background: #000;
    cursor: pointer;
}
.wpbingo-newsletter.newsletter-default .content-newsletter .clearfix:hover {
    background: #f03333;
}

.wpbingo-newsletter.newsletter-default .content-newsletter .clearfix:before {
    content: "\e904";
    font-family: icomoon;
    font-size: 20px;
    position: absolute;
    color: #fff;
    top: calc(50% - 12px);
    left: calc(50% - 10px);
}

.wpbingo-newsletter.newsletter-default .content-newsletter input[type=submit] {
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -ms-border-radius: 0;
    -o-border-radius: 0;
    font-size: 0;
    padding: 0 40px;
    position: relative;
    z-index: 2;
    background: transparent;
}


.wpcf7 .ajax-loader {
    visibility: hidden;
    display: inline-block;
    background-color: #23282d;
    opacity: .75;
    width: 24px;
    height: 24px;
    border: none;
    border-radius: 100%;
    padding: 0;
    margin: 0 24px;
    position: relative;
}
.wpbingo-newsletter.newsletter-default .content-newsletter .ajax-loader {
    position: absolute;
    left: calc(50% - 8px);
}
.wpcf7 .ajax-loader {
    margin: 0;
}